.end-container{
    display:flex;
    flex-direction: row;
    
    padding-left:150px;
    justify-content: center;
    ;
}
.cont{
    background-color:white;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.1);
    display:flex;
    flex-direction: row;
    padding:20px;
    border-radius:10px;
    margin-bottom:10px;
}
.icon-button{
   height: 37px;
    width: 142px;
    background-color: black;
    color: white;
    border-radius: 10px;
}
.box{
    display:flex;
    flex-direction: column;
    margin-right:20px;

}
.icon{
    width:50px;
    height:50px;
}
.icon-p{
    margin-top:-10px;
}

.cont2{
    display:flex;
    flex-direction: column;
    margin-left:50px;
}
.lady{
       height: 206px;
    width: 236px;
    margin-top: 18px;
    margin-bottom:10px;
}
.cont3{
    background-color:white;
    box-shadow: 10px 10px 30px rgba(0, 0, 0, 0.1);
   
    padding-left:20px;
    border-radius:10px;
    margin-bottom:10px;
}
.pp{
    margin-top:-37px;
}
@media (max-width: 768px) {
    .end-container{
        flex-direction: column;
        align-items: center;
        padding-left:0px;
        justify-content: center;;
    }
    .cont{
        flex-direction: column;
        align-items: center;
    }
    .cont2{
        margin-left:0px;
        align-items: center;
    }
    .cont3{
        align-items: center;
    }
}