.subscribe-container {
  background-color: #f9b13d;
  margin: 20px auto;
  border-radius: 20px;
  display: flex;
  flex-direction: column;
  padding: 32px 24px;
  align-items: center;
  max-width: 500px;
  box-shadow: 0 4px 16px 0 rgba(0,0,0,0.06);
}

.subscribe-text {
  text-align: center;
  margin-bottom: 20px;
}

.subscribe-heading {
  margin: 0 0 8px 0;
  font-size: 2rem;
  color: #222;
}

.subscribe-para {
  margin: 0;
  color: #222;
}

.subscribe-form {
  display: flex;
  width: 100%;
  justify-content: center;
  gap: 12px;
}

.email-input {
  padding: 10px 16px;
  border-radius: 8px;
  border: none;
  font-size: 1rem;
  flex: 1;
  min-width: 0;
  background: #fff;
}

.subscribe-button {
  background: #16866c;
  color: #fff;
  border: none;
  border-radius: 8px;
  padding: 10px 24px;
  font-size: 1rem;
  cursor: pointer;
  transition: background 0.2s;
}

.subscribe-button:hover {
  background: #12705d;
}

/* Responsive for mobile */
@media (max-width: 767px) {
  .subscribe-container {
    padding: 18px 8px;
    margin: 16px 8px;
    max-width: 100%;
  }
  .subscribe-form {
    flex-direction: column;
    gap: 10px;
  }
}
