/* Base styles are for desktop/laptop as currently done */

/* Tablet (768px to 1023px) - same as desktop */
@media (min-width: 768px) and (max-width: 1023px) {
  .main-container {
    flex-direction: row;
    margin-left: 30px;
    margin-top: 20px;
    margin-right: 50px;
    margin-bottom: 20px;
  }
  .ele{
    font-size:15px;
    margin-bottom:18px;
  }

  .img{
    width:129px;
    height:153px;
  }
  .img2 {
    width: 160px;
    height: 129px;
}

  .container {
    width: 45%;
    margin-right: 40px;
  }
  container2 {
    width:45%;
  }

  .section1 {
    flex-direction: column;
  }

  .section2 {
    flex-direction: row;
  }

  .card4 {
    width: 45%;
  }
}
@media (max-width: 767px) {
  .main-container {
    flex-direction: column;
    margin-left: 15px;
    margin-right: 15px;
    margin-top: 20px;
    margin-bottom: 20px;
  }

  .container, .container2 {
    width: 100%;
    margin-right: 0;
  }

  .section1, .section2 {
    flex-direction: column;
  }

  /* Make all cards take full width, stack vertically */
  .card1, .card2, .card3, .card4 {
    width: 100%;
    margin-bottom: 15px;
  }

  /* Adjust images to be smaller and fit container */
  .img, .img2, .img3, .imgm {
    width: 100%;
    height: auto;
    max-width: 250px; /* optional max width */
    margin-top: 10px;
  }

  /* Center buttons and make them wider for easier tap */
  .card1-button, .card4-button {
    width: 80%;
    margin: 10px auto 0 auto;
    display: block;
  }

  /* Add padding within cards for better spacing */
  .card1, .card2, .card3 {
    padding-left: 10px;
    padding-right: 10px;
  }
  /* Set all cards to have the same height and width */
.card1, .card2, .card3, .card4 {
  background-color: whitesmoke;
  height: 230px; /* Use the height of the tallest card */
  border-radius: 20px;
  padding: 20px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  width: 100%; /* Full width of container */
  margin-bottom: 20px; /* consistent spacing between cards */
  box-sizing: border-box; /* include padding in width */
}

/* For .card4 which previously had column layout, adjust to row for uniformity */
.card4 {
  flex-direction: row;
  align-items: center;
}

/* Adjust the image containers to have same proportions for all cards */
.card1sub2, .card3sub2, .card4 img, .img, .img2, .img3, .imgm {
  width: 200px;
  height: 200px;
  object-fit: contain; /* keep image aspect ratio */
  margin-left: 20px;
}

/* Adjust text container width uniformly */
.card1sub1, .card3sub1, .card4 > div {
  width: calc(100% - 220px); /* leaves space for image + margin */
}

/* Buttons styling with consistent size */
.card1-button, .card4-button {
  background-color: black;
  color: white;
  height: 35px;
  border-radius: 10px;
  width: 120px;
  margin-top: 10px;
}

/* Mobile view override for stacking items vertically */
@media (max-width: 767px) {
  .card1, .card2, .card3, .card4 {
    flex-direction: column;
    height: auto;
    padding: 15px;
  }

  .card1sub1, .card3sub1, .card4 > div {
    width: 100%;
  }

  .card1sub2, .card3sub2, .card4 img, .img, .img2, .img3, .imgm {
    width: 100%;
    height: auto;
    margin-left: 0;
    margin-top: 15px;
  }

  .card1-button, .card4-button {
    width: 100%;
    height: 40px;
    margin: 15px 0 0 0;
  }
}

}


/* Mobile (320px to 767px) - single column layout */

